<template>
    <div class="home">
        <div v-if="IS_CONNECTED">
            <div class="home__inputs">
                <button @click="BECOME_MEMBER" type="button" class="btn btn-primary">Become Part of Community</button>
                </div>
                <button @click="GET_MEMBER_PROPOSALS" type="button" style="margin-top: 20px;" class="btn btn-outline-warning">Update cards</button>
                <div class="home__cards">
                    <Member :proposal="proposal" v-for="proposal, index in MEMBER_PROPOSALS" :key="index" />
                </div>
        </div>
        <div v-else class="mx-auto">
            <button @click="CONNECT_METAMASK" type="button" class="btn btn-warning btn-lg">Connect Wallet</button>
        </div>
        <Window/>
        <Loader v-if="LOADER" />
    </div>    
</template>
  
  <script>
  import { mapGetters, mapActions } from 'vuex'
  import Loader from "../components/Loader";
  import Member from "../components/Member";
  import Window from "../components/Window.vue";

  
  export default {
    async created() {
        await this.GET_MEMBER_PROPOSALS();
    },
    components: {
      Loader,
      Member,
      Window
    },
    computed: {
    ...mapGetters(['IS_CONNECTED','LOADER', 'MEMBER_PROPOSALS'])
    },
    methods: {
    ...mapActions(['CONNECT_METAMASK', 'BECOME_MEMBER', 'GET_MEMBER_PROPOSALS'])
    }
  }
  </script>

<style scoped>
.home__inputs {
  width: 900px;
  margin: 0 auto;
}
.home__cards {
  width: 900px;
  margin: 20px auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
.card {
  margin-bottom: 30px;
}
</style>