import Vue from 'vue'
import Vuex from 'vuex'
import {
  connect,
  checkAddress,
  becomeMember, 
  fetchContractData,
  finishMemberVoiting,
  voteForMember,
  becomeOracle,
  voteForOracle,
  finishOracleVoiting
} from '@/utils/web3.js';

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    loader: false,
    isConnected: false,
    message: '',
    memberProposals: [],
    oracleProposals: []
  },
  getters: {
    IS_CONNECTED: state => state.isConnected,
    MESSAGE: state => state.message,
    LOADER: state => state.loader,
    MEMBER_PROPOSALS: state => state.memberProposals,
    ORACLE_PROPOSALS: state => state.oracleProposals
  },
  mutations: {
    SET_CONNECT_STATUS: (state, status) => state.isConnected = status, 
    SET_LOADER_STATUS: (state, status) => state.loader = status, 
    SET_MESSAGE: (state, msg) => state.message = msg,
    SET_MEMBER_PROPOSALS: (state, arr) => state.memberProposals = arr,
    SET_ORACLE_PROPOSALS: (state, arr) => state.oracleProposals = arr
  },
  actions: {
    async CONNECT_METAMASK({commit}) {
      commit('SET_LOADER_STATUS', true);
      const status = await connect();
      if(status) {
        commit('SET_CONNECT_STATUS', true);
      }
      commit('SET_LOADER_STATUS', false);
    },
    async BECOME_MEMBER({commit}) {
      commit('SET_LOADER_STATUS', true);
      commit('SET_MESSAGE', '');
      const addresses = await checkAddress();
      if(addresses) {
        const message = await becomeMember();
        if (message.hash) {
          commit('SET_MESSAGE', `https://goerli.etherscan.io/tx/${message.hash}`);
        } else if (message.error.message) {
          commit('SET_MESSAGE', message.error.message);
        }
      } else {
        commit('SET_CONNECT_STATUS', false);
      }
      commit('SET_LOADER_STATUS', false);
    },
    async BECOME_ORACLE({commit}, data) {
      commit('SET_LOADER_STATUS', true);
      commit('SET_MESSAGE', '');
      const addresses = await checkAddress();
      if(addresses) {
        const message = await becomeOracle(data.metricName, data.tag, data.description);
        if (message.hash) {
          commit('SET_MESSAGE', `https://goerli.etherscan.io/tx/${message.hash}`);
        } else if (message.error.message) {
          commit('SET_MESSAGE', message.error.message);
        } 
      } else {
        commit('SET_CONNECT_STATUS', false);
      }
      commit('SET_LOADER_STATUS', false);
    },
    async GET_MEMBER_PROPOSALS({commit}) {
      commit('SET_MEMBER_PROPOSALS', []);
      const r = await fetchContractData('getMemberProposals', []);
      let index = 0;
      const array = r.map(el => ({ endTime: el.endTime, member: el.member, support: el.support, id: index++ }) )
      commit('SET_MEMBER_PROPOSALS', array);
    },
    async GET_ORACLE_PROPOSALS({commit}) {
      commit('SET_ORACLE_PROPOSALS', []);
      const r = await fetchContractData('getOracleProposals', []);
      let index = 0;
      const array = r.map(el => ({ metricName: el.metricName, description: el.description, tag: el.tag, endTime: el.endTime, oracle: el.oracle, support: el.support, id: index++ }) )
      commit('SET_ORACLE_PROPOSALS', array);
    },
    async FINISH_MEMBER_PROPOSAL({commit}, id) {
      commit('SET_LOADER_STATUS', true);
      commit('SET_MESSAGE', '');
      const addresses = await checkAddress();
      if(addresses) {
        const message = await finishMemberVoiting(id);
        if (message.hash) {
          commit('SET_MESSAGE', `https://goerli.etherscan.io/tx/${message.hash}`);
        } else if (message.error.message) {
          commit('SET_MESSAGE', message.error.message);
        }
      } else {
        commit('SET_CONNECT_STATUS', false);
      }
      commit('SET_LOADER_STATUS', false);
    },
    async VOTE_MEMBER_PROPOSAL({commit}, id) {
      commit('SET_LOADER_STATUS', true);
      commit('SET_MESSAGE', '');
      const addresses = await checkAddress();
      if(addresses) {
        const message = await voteForMember(id);
        if (message.hash) {
          commit('SET_MESSAGE', `https://goerli.etherscan.io/tx/${message.hash}`);
        } else if (message.error.message) {
          commit('SET_MESSAGE', message.error.message);
        }
      } else {
        commit('SET_CONNECT_STATUS', false);
      }
      commit('SET_LOADER_STATUS', false);
    },
    async FINISH_ORACLE_PROPOSAL({commit}, id) {
      commit('SET_LOADER_STATUS', true);
      commit('SET_MESSAGE', '');
      const addresses = await checkAddress();
      if(addresses) {
        const message = await finishOracleVoiting(id);
        if (message.hash) {
          commit('SET_MESSAGE', `https://goerli.etherscan.io/tx/${message.hash}`);
        } else if (message.error.message) {
          commit('SET_MESSAGE', message.error.message);
        }
      } else {
        commit('SET_CONNECT_STATUS', false);
      }
      commit('SET_LOADER_STATUS', false);
    },
    async VOTE_ORACLE_PROPOSAL({commit}, id) {
      commit('SET_LOADER_STATUS', true);
      commit('SET_MESSAGE', '');
      const addresses = await checkAddress();
      if(addresses) {
        const message = await voteForOracle(id);
        if (message.hash) {
          commit('SET_MESSAGE', `https://goerli.etherscan.io/tx/${message.hash}`);
        } else if (message.error.message) {
          commit('SET_MESSAGE', message.error.message);
        }
      } else {
        commit('SET_CONNECT_STATUS', false);
      }
      commit('SET_LOADER_STATUS', false);
    }
  },
  modules: {
  }
})
