<template>
  <div class="home">
    <div v-if="IS_CONNECTED">
      <div class="home__inputs">
        <div class="input-group mb-3">
          <span class="input-group-text" id="inputGroup-sizing-default">Metric Name</span>
          <input v-model="metricName" type="text" class="form-control" aria-describedby="inputGroup-sizing-default">
        </div>
        <div class="input-group mb-3">
          <span class="input-group-text" id="inputGroup-sizing-default">Tag</span>
          <input v-model="tag" type="text" class="form-control" aria-describedby="inputGroup-sizing-default">
        </div>
        <div class="input-group mb-3">
          <span class="input-group-text" id="inputGroup-sizing-default">Description</span>
          <input v-model="description" type="text" class="form-control" aria-describedby="inputGroup-sizing-default">
        </div>
        <button @click="handleBecomeOracle" type="button" class="btn btn-primary">Become Oracle</button>
      </div>
      <button @click="GET_ORACLE_PROPOSALS" type="button" style="margin-top: 20px;" class="btn btn-outline-warning">Update cards</button>
      <div class="home__cards">
        <Oracle :proposal="proposal" v-for="proposal, index in ORACLE_PROPOSALS" :key="index" />
      </div>
    </div>
    <div v-else class="mx-auto">
      <button @click="CONNECT_METAMASK" type="button" class="btn btn-warning btn-lg">Connect Wallet</button>
    </div>
    <Window/>
    <Loader v-if="LOADER" />
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import Loader from "../components/Loader";
import Oracle from "../components/Oracle.vue";
import Window from "../components/Window.vue";

export default {
  data() {
        return {
          metricName: '',
          tag: '',
          description: '',
        }
    },
  name: 'Main',
  async created() {
      await this.GET_ORACLE_PROPOSALS();
  },
  components: {
    Loader,
    Oracle,
    Window
  },
  computed: {
    ...mapGetters(['IS_CONNECTED','LOADER','ORACLE_PROPOSALS'])
  },
  methods: {
    ...mapActions(['CONNECT_METAMASK','GET_ORACLE_PROPOSALS','BECOME_ORACLE']),
    handleBecomeOracle() {
      if(!this.description) {
        this.$vToastify.info('Fill description', 'Validation warning')
      } else if (this.tag && this.metricName) {
        this.$vToastify.info('Fill only tag or metric name', 'Validation warning')
      } else if (!this.tag && !this.metricName) {
        this.$vToastify.info('Fill tag or metric name', 'Validation warning')
      } else {
        const data = { metricName: this.metricName, tag: this.tag, description: this.description };
        this.BECOME_ORACLE(data);
      }
    }
  },
}
</script>

<style scoped>
.home__inputs {
  width: 900px;
  margin: 0 auto;
}
.home__cards {
  width: 900px;
  margin: 20px auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
.card {
  margin-bottom: 30px;
}
</style>
