<template>
    <div class="card" style="width: 18rem;">
        <div class="card-body">
          <p class="card-title">{{proposal.member}}</p>
        </div>
        <ul class="list-group list-group-flush">
          <li class="list-group-item">{{endTime}}</li>
          <li class="list-group-item">{{proposal.support}}</li>
        </ul>
        <div class="card-body">
          <button @click="VOTE_MEMBER_PROPOSAL(proposal.id)" v-if="isVoteTime" type="button" class="btn btn-secondary">Vote</button>
          <button @click="FINISH_MEMBER_PROPOSAL(proposal.id)" v-else type="button" class="btn btn-secondary">Finish</button>
        </div>
    </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'

export default {
    created() {
        this.date = this.proposal.endTime
    },
    data() {
        return {
            date: '',
        }
    },
    props: {
        proposal: Object,
    },
    computed: {
        endTime() {
            return new Date(this.date * 1000); 
        },
        isVoteTime() {
            return Date.now() <= this.date * 1000
        }
    },
    methods: {
    ...mapActions(['FINISH_MEMBER_PROPOSAL','VOTE_MEMBER_PROPOSAL'])
    }
}
</script>