const { ethers } = require("ethers");
const { abi } = require('@/utils/abi');
const BigNumber = require('bignumber.js');
const Web3 = require('web3');

BigNumber.config({ EXPONENTIAL_AT: 60 });

const contractAddress = "0x5723FF0134223f1151D6A0De74F13880e8c8F65D";
const bscUrl = "https://rpc.ankr.com/eth_goerli";

const methodSwitchEthTestnet = {
    method: 'wallet_switchEthereumChain',
    params: [{ chainId: '0x5' }],
};

let provider;
let viewProvider;
let contract;

async function connect() {
    if (typeof window.ethereum !== "undefined") {
      try {
        await ethereum.request({ method: "eth_requestAccounts" });
        provider = new ethers.providers.Web3Provider(window.ethereum);
        const { chainId } = await provider.getNetwork();
        if ((chainId !== 5)) {
            await ethereum.request(methodSwitchEthTestnet);
        }
        return true;
      } catch (error) {
        console.log(error);
        return false;
      }
    } else {
      console.log("Please install MetaMask");
      return false;
    }
  }

  
const connectNode = () => {
    try {
      const provider = new Web3.providers.HttpProvider(bscUrl)
      viewProvider = new Web3(provider)
      return true
    } catch (e) {
      return false
    }
  }

 async function checkAddress() {
    const accounts = await ethereum.request({ method: "eth_accounts" });
    return accounts.length;
 } 

 async function getInstance() {
    if (!contract) {
      const signer = provider.getSigner();
      contract = new ethers.Contract(contractAddress, abi, signer);
    }
 }

 const fetchContractData = async (method, params) => {
    if(!viewProvider) {
        connectNode()
    }
    try {
      const contract = new viewProvider.eth.Contract(abi, contractAddress)
      return await contract.methods[method].apply(this, params).call()
    } catch (e) {
      console.log(e)
      return ''
    }
  } 

 async function becomeOracle(metric, tag, description) {
    await getInstance();
    try {
      const tx = await contract.becomeOracle(metric, tag, description);
      return tx;
    } catch (error) {
      console.log(error);
      return error;
    }
 }

 async function voteForOracle(id) {
    await getInstance();
    try {
      const tx = await contract.voteForOracle(id);
      return tx;
    } catch (error) {
      console.log(error);
      return error;
    }
 }

 async function finishOracleVoiting(id) {
    await getInstance();
    try {
      const tx = await contract.finishOracleVoiting(id);
      return tx;
    } catch (error) {
      console.log(error);
      return error;
    }
 }

 async function becomeMember() {
    await getInstance();
    try {
      const tx = await contract.becomeMember();
      return tx;
    } catch (error) {
      console.log(error);
      return error;
    }
 }

 async function voteForMember(id) {
    await getInstance();
    try {
      const tx = await contract.voteForMember(id);
      return tx;
    } catch (error) {
      console.log(error);
      return error;
    }
 }

 async function finishMemberVoiting(id) {
    await getInstance();
    try {
      const tx = await contract.finishMemberVoiting(id);
      return tx;
    } catch (error) {
      console.log(error);
      return error;
    }
 }



 module.exports = {
    connect,
    connectNode,
    checkAddress,
    fetchContractData,
    becomeOracle,
    voteForOracle,
    finishOracleVoiting,
    becomeMember,
    voteForMember,
    finishMemberVoiting
};